  // ***********************************************************
  // ---------------- ARMADO DEL CALENDARIO
  // ***********************************************************

  // Next, create the function for push calendar date items to the arrays that will be displaying the full calendar date.

  getDaysOfMonth() {
    this.daysInThisMonth = new Array();
    this.daysInLastMonth = new Array();
    this.daysInNextMonth = new Array();
    this.currentMonth = this.monthNames[this.date.getMonth()];
    this.currentYear = this.date.getFullYear();
    if (this.date.getMonth() === new Date().getMonth()) {
      this.currentDate = new Date().getDate();
    } else {
      this.currentDate = 999;
    }

    var firstDayThisMonth = new Date(
      this.date.getFullYear(),
      this.date.getMonth(),
      1
    ).getDay();
    var prevNumOfDays = new Date(
      this.date.getFullYear(),
      this.date.getMonth(),
      0
    ).getDate();
    for (
      var i = prevNumOfDays - (firstDayThisMonth - 1);
      i <= prevNumOfDays;
      i++
    ) {
      this.daysInLastMonth.push(i);
    }

    var thisNumOfDays = new Date(
      this.date.getFullYear(),
      this.date.getMonth() + 1,
      0
    ).getDate();
    for (var i = 0; i < thisNumOfDays; i++) {
      this.daysInThisMonth.push(i + 1);
    }
    var lastDayThisMonth = new Date(
      this.date.getFullYear(),
      this.date.getMonth() + 1,
      0
    ).getDay();
    var nextNumOfDays = new Date(
      this.date.getFullYear(),
      this.date.getMonth() + 2,
      0
    ).getDate();
    for (var i = 0; i < 6 - lastDayThisMonth; i++) {
      this.daysInNextMonth.push(i + 1);
    }
    var totalDays =
      this.daysInLastMonth.length +
      this.daysInThisMonth.length +
      this.daysInNextMonth.length;
    if (totalDays < 36) {
      for (var i = 7 - lastDayThisMonth; i < 7 - lastDayThisMonth + 7; i++) {
        this.daysInNextMonth.push(i);
      }
    }
  }
  // Now, add the function for previous month button action.
  goToLastMonth() {
    this.date = new Date(this.date.getFullYear(), this.date.getMonth(), 0);
    this.getDaysOfMonth();
  }
  // Also for the next month button action.
  goToNextMonth() {
    this.date = new Date(this.date.getFullYear(), this.date.getMonth() + 2, 0);
    this.getDaysOfMonth();
    this.selectedDay = null;
  }

  // ***********************************************************
  // ---------------- APIS
  // ***********************************************************

  fechasocupadas: any = [];

  getFechasOcupadas() {
    this.apiAdm.getFechasOcupadas().subscribe(x => {
      this.eventList = x["data"];
      // console.log(this.eventList);
    });
  }

  // *************************************************
  // ---------------- CHECKEO DE EVENTOS EN EL MES
  // *************************************************

  checkEvent(day) {
    let myDate = this.getFullDate(day);
    let thisDate = this.datePipe.transform(myDate, "yyyy-MM-dd");
    let hasEvent = 0;

    this.eventList.forEach(event => {
      if (event.fecha == thisDate && event.cantidadcitas <= 7) {
        hasEvent = 1;
      } else if (event.fecha == thisDate && event.cantidadcitas >= 8) {
        hasEvent = 2;
      }
    });
    return hasEvent;
  }

  // ***********************************************************
  // ---------------- SELECCION
  // ***********************************************************

  selectedDateSend: any;
  selectedDate: any;
  selectedDay: any;

  selectDate(day) {
    // dia pref para admin
    let diaPreferidoNumA = new Date(this.cita.diapreferido.replace(/-/g, "/"));
    let diaPreferidoNum = this.datePipe.transform(diaPreferidoNumA, "yyyyMMdd");

    // --- Día seleccionado
    let newDate = new Date(this.getFullDate(day));
    let selected = this.datePipe.transform(newDate, "yyyyMMdd");
    // --- Ayer del día destacado
    let diaDestacadoAyer = this.getPreviousDay(this.diaDestacado, 1);
    let diaDestacadoAyerOrder = this.datePipe.transform(
      diaDestacadoAyer,
      "yyyyMMdd"
    );
    // --- Dos semanas permitidas DESDE EL DIAPREFERIDO (a diferencia del scouter)
    let dosSemanas = this.getNextDay(this.diaDestacado, 19);
    let dosSemanasOrder = this.datePipe.transform(dosSemanas, "yyyyMMdd");
    // -- Dias posibles para seleccionar
    if (+selected <= +dosSemanasOrder && +selected > +diaPreferidoNum) {
      console.log("Llegó: ", newDate.getDay());
      // --- Descarta fines de semana
      if (newDate.getDay() !== 6 && newDate.getDay() !== 0) {
        this.selectedDay = newDate.getDate();
        this.selectedDate = this.datePipe.transform(newDate, "dd-MM-yyyy");
        this.selectedDateSend = this.datePipe.transform(newDate, "yyyy-MM-dd");
      } else {
        this.selectedDate = "Debe ser un día hábil";
      }
    } else {
      this.selectedDate =
        "Debe ser hasta dos semanas y 3 días hábiles después del día " +
        this.diaDestacado.getDate() +
        " de este mes";
    }
  }

  // ***********************************************************
  // ---------------- SELECT DE DIAS HÁBILES Y DESTACADO
  // ***********************************************************

  getDiaDestacado() {
    let newDate = new Date();
    var a = this.getNextDay(newDate, 7);
    this.diaDestacado = a;
    this.diaDestacadoA = a.getDate();
  }

  getDiaDestacadoAdmin() {
    let newDate = new Date(this.cita.diapreferido.replace(/-/g, "/"));
    this.diaDestacado = newDate;
    this.diaDestacadoA = newDate.getDate();
  }

  getFullDate(day: number) {
    let currentYear = this.date.getFullYear();
    let currentMonth = this.date.getMonth() + 1;
    let currentDay = day;
    let fullDate = currentYear + "-" + currentMonth + "-" + currentDay;
    return fullDate;
  }

  getNextDay(date, num) {
    let nextDay = new Date();
    nextDay.setDate(date.getDate() + num);
    return nextDay;
  }

  getPreviousDay(date, num) {
    let previous = new Date();
    previous.setDate(date.getDate() - num);
    return previous;
  }

  showDiaDestacado(day) {
    if (
      this.diaDestacadoA == day &&
      this.checkOnlyThisMonth(this.diaDestacado)
    ) {
      return true;
    } else {
      return false;
    }
  }

  checkOnlyThisMonth(diahabil) {
    var a = new Date().getMonth();
    var b = this.date.getMonth();
    var c = diahabil.getMonth();

    if (a == b && b == c) {
      return true;
    } else {
      return false;
    }
  }

  // checkFinde(day) {
