  //   // console.log("Día, ", fullDate.getDay());
  //   return resolve;
  // }

  // ***********************************************************
  // ---------------- BOTONES
  // ***********************************************************

  aceptarFecha() {
    let data = {
      selectedDateSend: this.selectedDateSend,
      selectedDate: this.selectedDate
    };
    console.log("calen data", data);
    this.apiAdm.params = { data: data, desde: "calendario" };
    this.navCtrl.pop();
  }

  guardarFecha() {
    let data = {
      selectedDateSend: this.selectedDateSend,
      selectedDate: this.selectedDate
    };
    this.viewCtrl.dismiss(data);
  }

  goBack() {
    this.navCtrl.pop();
  }
