import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController
} from "ionic-angular";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";

@IonicPage()
@Component({
  selector: 'page-editar-permisosbk',
  templateUrl: 'editar-permisosbk.html',
})
export class EditarPermisosbkPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public apiAdm: ApiAdmProvider,
    public alertCtrl: AlertController
  ) {}

  usuario: any = {};
  // ----
  usuarioid: string;
  nombreusuario: string;
  email: string;
  password: string;
  rol: string;
  estado: string;
  activo: string;

  ionViewDidLoad() {
    this.usuario = this.navParams.get("user");
    // ----
    this.usuarioid = this.usuario.usuarioid;
    this.nombreusuario = this.usuario.nombreusuario;
    this.email = this.usuario.email;
    this.rol = this.usuario.rol;
    this.estado = this.usuario.estado;
    this.activo = this.usuario.activo;
    this.password = this.usuario.password;
    //
    console.log(this.usuario);
  }

  editar() {
    this.apiAdm
      .editarUsuario(this.usuarioid,
        this.nombreusuario,
        this.email,
        "1",
        this.password,
        this.estado,
        "1"
      )
      .subscribe(x => {
        this.alertEditado();
        console.log(x);
      });
  }

  alertEditado() {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message: "Usuario editado con éxito.",

      buttons: [
        {
          text: "Ok",
          handler: data => {
            this.navCtrl.pop();
          }
        }
      ]
    });
    prompt.present();
  }

  goBack() {
    this.navCtrl.pop();
  }
}



