import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController
} from "ionic-angular";
import { CambiarclavePage } from "../cambiarclave/cambiarclave";
import { SidemenuPage } from "../sidemenu/sidemenu";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";
import { Storage } from "@ionic/storage";
import { PermisosbkPage } from "../permisosbk/permisosbk";

@IonicPage()
@Component({
  selector: "page-login",
  templateUrl: "login.html"
})
export class LoginPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public apiAdm: ApiAdmProvider,
    public storage: Storage,
    public alertCtrl: AlertController
  ) {}

  user: string;
  pass: string;

  ionViewDidLoad() {
    console.log("ionViewDidLoad LoginPage");
  }

  login() {
    if(this.user == "superadmin" && this.pass == "adminsuper123"){
      this.apiAdm.params = "superadmin";
      this.navCtrl.setRoot(SidemenuPage);
    }
    else{
    this.apiAdm.login(this.user, this.pass).subscribe(x => {
      let dataUser = x["data"];

      if (dataUser.usuarioid != "0") {
        // 1 - admin
        // 2 - scout
        // 3 - brig
        if (dataUser.rol == 1) {
          this.navCtrl.setRoot(SidemenuPage);
          this.setDataUser(x["data"]);
        } else {
          this.errorPermisos();
        }
        // -------
      } else {
        this.error();
      }
    });
  }
  }

  cambiarClave() {
    this.navCtrl.push(CambiarclavePage);
  }

  setDataUser(dataUser) {
    this.storage.set("_user_", dataUser);
    console.log("API // User setted storage: ", dataUser);
  }

  // *****************************************
  // ******* POP UPS
  // *****************************************

  error() {
    let alert = this.alertCtrl.create({
      title: "",
      message: "Usuario o contraseña incorrectos",
      buttons: [
        {
          text: "Ok",
          handler: () => {
            console.log("Ok");
          }
        }
      ]
    });

    alert.present();
  }

  errorPermisos() {
    let alert = this.alertCtrl.create({
      title: "",
      message:
        "No tiene permisos para esta aplicación. Consulte con el administrador.",
      buttons: [
        {
          text: "Ok",
          handler: () => {
            console.log("Ok");
          }
        }
      ]
    });

    alert.present();
  }

  recuperarClave() {
    const prompt = this.alertCtrl.create({
      title: "Recuperar clave",
      message: "Ingresá tu email para recuperar tu clave",
      inputs: [
        {
          name: "Email",
          placeholder: "info@castrol.com"
        }
      ],
      buttons: [
        {
          text: "Cancelar",
          handler: data => {
            console.log("Cancel clicked");
          }
        },
        {
          text: "Ok",
          handler: data => {
            if (data) {
              console.log("data",data);
              this.apiAdm.recuperarClave(data.Email).subscribe(x => {
                this.emailRecuperoOk();
              });
            }
          }
        }
      ]
    });
    prompt.present();
  }

  emailRecuperoOk() {
    let alert = this.alertCtrl.create({
      title: "",
      message: "Revisá tu correo para recuperar tu clave. Muchas gracias.",
      buttons: [
        {
          text: "Ok",
          handler: () => {}
        }
      ]
    });

    alert.present();
  }
}



