import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController
} from "ionic-angular";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";
import { EditarPermisosbkPage } from "../editar-permisosbk/editar-permisosbk";
import { SidemenuPage } from "../sidemenu/sidemenu";

/**
 * Generated class for the PermisosbkPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-permisosbk',
  templateUrl: 'permisosbk.html',
})
export class PermisosbkPage {

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public apiAdm: ApiAdmProvider,
    public alertCtrl: AlertController
  ) {}

  estado: string;
  usuarios: any = [];
  read: boolean = true;
  // ----
  usuarioScout: string;
  passwordScout: string;
  emailScout: string;
  activoScout: string;
  // ----
  usuarioBrig: string;
  passwordBrig: string;
  emailBrig: string;
  activoBrig: string;

  ionViewWillEnter() {
    this.getUsers();
  }

  getUsers() {
    this.apiAdm.getUsersBackOffice().subscribe(x => {
      this.usuarios = x["data"];
      console.log(x["data"]);
    });
  }

  alta(num) {
    
      this.apiAdm
        .altaUsuario(
          this.usuarioScout,
          this.passwordScout,
          this.emailScout,
          this.estado,
          "1",
            1
        )
        .subscribe(x => {
          this.done();
          this.navCtrl.setRoot(SidemenuPage);
        });
    
  }

  // ALERTS

  done() {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message: "¡Usuario creado con éxito!",

      buttons: [
        {
          text: "Ok",
          handler: data => {
            this.estado = "";
            // ----
            this.usuarioScout = "";
            this.passwordScout = "";
            this.emailScout = "";
            // ----
            this.usuarioBrig = "";
            this.passwordBrig = "";
            this.emailBrig = "";
            // this.navCtrl.setRoot(GestioncitasPage);
          }
        }
      ]
    });
    prompt.present();
  }

  setRol(num) {
    switch (num) {
      case "2":
        return "Scouting";
      case "3":
        return "Brigada";
      default:
        break;
    }
  }

  setActivo(num) {
    switch (num) {
      case "1":
        return "Activo";
      case "0":
        return "Inactivo";
      default:
        break;
    }
  }

  borrarUsuario(user) {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message:
        "¿Está seguro que desea borrar el usuario " + user.nombreusuario + "?",

      buttons: [
        {
          text: "Sí",
          handler: data => {
            this.apiAdm.borrarUsuario(user).subscribe(x => {
              this.alertBorrado();
            });
          }
        },
        {
          text: "Cancelar",
          handler: data => {}
        }
      ]
    });
    prompt.present();
  }

  alertBorrado() {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message: "Usuario borrado con éxito.",

      buttons: [
        {
          text: "Ok",
          handler: data => {
            this.ionViewWillEnter();
          }
        }
      ]
    });
    prompt.present();
  }

  editarUsuario(user) {
    this.navCtrl.push(EditarPermisosbkPage, {
      user: user
    });
  }
}



