import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController
} from "ionic-angular";
import { GestioncitasPage } from "../gestioncitas/gestioncitas";
import { DatePipe } from "@angular/common";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";
import { SidemenuPage } from "../sidemenu/sidemenu";
import { CalendarioPage } from "../calendario/calendario";
import { MapPage } from "../map/map";

@IonicPage()
@Component({
  selector: "page-confirmacion",
  templateUrl: "confirmacion.html"
})
export class ConfirmacionPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private datePipe: DatePipe,
    public apiAdm: ApiAdmProvider,
    public alertCtrl: AlertController
  ) {}

  reagendar: boolean;
  cita: any = [];
  fullCitas: any = [];
  editionMode: boolean;
  title: string = "CONFIRMACIÓN";
  hora: any = 12;
  minutos: any = 30;
  orderedDate: any;
  // params de confirmación
  visitaId: any;
  turno: any;
  horaCita: any;
  diaPreferido: string;
  confirmado: string;
  brigadas: any = [];
  brigadaSeleccionada: any;
  validarGuardar: boolean = false;

  // ---
  diapreferidoForShow: any;

  ionViewWillEnter() {
    if (
      this.apiAdm.params != null &&
      this.apiAdm.params.desde == "calendario"
    ) {
      var fechadata = this.apiAdm.params.data;
      //console.log("fecha data", fechadata);
      this.diaPreferido = fechadata.selectedDateSend;
    }
    this.apiAdm.getBrigadasVisitas().subscribe(x => {
      this.brigadas = x["data"];
    });
  }

  ionViewDidLoad() {
    this.cita = this.navParams.get("cita");
    this.fullCitas = this.navParams.get("citas");
    this.reagendar = this.navParams.get("reagendar");

    // ----
    this.editionMode = this.navParams.get("editionMode");
    this.orderedDate = this.order(this.cita.diapreferido);

    this.getNavParams();

    if (this.editionMode) {
      this.title = "MODIFICACIÓN";
      this.hora = this.getHora(this.cita.horacita, 1);
      this.minutos = this.getHora(this.cita.horacita, 0);
      console.log(this.hora, " ", this.minutos);
      this.brigadaSeleccionada = this.cita.brigadaid;
      this.validarGuardar = true;
      console.log("bri sele", this.brigadaSeleccionada);
    }
  }

  getNavParams() {
    // params de confirmación
    this.visitaId = this.cita.visitaid;
    this.turno = this.cita.turno;
    this.confirmado = this.cita.confirmado;
    this.diaPreferido = this.cita.diapreferido;
    // Date for show
    let dateForShow = new Date(this.cita.diapreferido.replace(/-/g, "/"));
    this.diapreferidoForShow = this.datePipe.transform(
      dateForShow,
      "dd/MM/yyyy"
    );
  }

  radioSeleccionado() {
    this.validarGuardar = true;
  }

  // ***********************************************************
  // ---------------- Navegación
  // ***********************************************************

  goToCalendar() {
    this.navCtrl.push(CalendarioPage, {
      botones: true,
      cita: this.cita
    });
  }

  goToMap() {
    this.navCtrl.push(MapPage, {
      citas: this.fullCitas,
      diaElegido: this.diaPreferido,
      dayMap: true
    });
  }

  goBack() {
    this.navCtrl.pop();
  }

  confirm() {
    console.log("brigada selected", this.brigadaSeleccionada);
    let confirmado = "1";
    this.horaCita = this.hora.toString() + ":" + this.minutos.toString();

    this.apiAdm
      .confirmarCita(
        this.visitaId,
        this.horaCita,
        this.diaPreferido,
        confirmado,
        this.brigadaSeleccionada
      )
      .subscribe(x => {
        console.log("CONFIRMADO: ", x);
        this.done();
      });
  }
  // ***********************************************************
  // ---------------- Calendario
  // ***********************************************************

  addEvent(event) {
    // --- Como se muestra en el DOM el día que selecciono
    let selected = event.targetElement.value;
    let selectedDate = new Date(selected);
    let selectedDateDMY = this.datePipe.transform(selectedDate, "dd/MM/yyyy");
    event.targetElement.value = selectedDateDMY;
    // --- Día seleccionado para guardar
    let selectedDateYMD = this.datePipe.transform(selectedDate, "yyyy-MM-dd");
    this.diaPreferido = selectedDateYMD;
  }

  dateToNum(fecha) {
    let date = new Date(fecha);
    let datenum = this.datePipe.transform(date, "yyyyMMdd");
    return +datenum;
  }

  order(fecha) {
    let newDate = new Date(fecha.replace(/-/g, "/"));
    let ordered = this.datePipe.transform(newDate, "dd/MM/yyyy");
    return ordered;
  }

  // ***********************************************************
  // ---------------- Alerts
  // ***********************************************************

  done() {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message: "¡Cita confirmada con éxito!",

      buttons: [
        {
          text: "Ok",
          handler: data => {
            console.log("Saved clicked");
            this.navCtrl.setRoot(SidemenuPage);
          }
        }
      ]
    });
    prompt.present();
  }

  // Edición

  getHora(horario, num) {
    for (let i = 0; i < horario.length; i++) {
      const element = horario[i];
      var a = horario[0];
      var b = horario[1];
      var c = horario[2];
      var d = horario[3];
      var e = horario[4];
    }

    if (num == 1) {
      return a + b;
    } else {
      return d + e;
    }
  }

  // ***********************************************************
  // ---------------- Set Status
  // ***********************************************************

  setStatus(num) {
    switch (num) {
      case 1:
        return "Pendiente";
      case 2:
        return "Pospuesto";
      case 3:
        return "No acepta preguntas";
      case 4:
        return "Cerrado";
      default:
        break;
    }
  }

  setConfirm(num) {
    switch (num) {
      case "1":
        return "Pendiente";
      case "2":
        return "Confirmado";
      case "3":
        return "Cancelado";
      default:
        break;
    }
  }

  setTurno(turno) {
    switch (turno) {
      case "1":
        return "Mañana";
      case "2":
        return "Tarde";
      case "3":
        return "Noche";
      default:
        break;
    }
  }

  dateForShow(date) {
    if (date) {
      // Date for show
      let dateForShow = new Date(date.replace(/-/g, "/"));
      let dateForShowB = this.datePipe.transform(dateForShow, "dd/MM/yyyy");
      return dateForShowB;
    } else {
      return "DD/MM/AAAA";
    }
  }
}



