import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams } from "ionic-angular";
import { DatePipe } from "@angular/common";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";

@IonicPage()
@Component({
  selector: "page-descargar-talleres",
  templateUrl: "descargar-talleres.html"
})
export class DescargarTalleresPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private datePipe: DatePipe,
    public apiAdm: ApiAdmProvider
  ) {}

  fechaDesde: string;
  fechaHasta: string;
  filtro: any = 1;
  ionViewDidLoad() {
    console.log("ionViewDidLoad DescargarTalleresPage");
  }

  addEvent(event) {
    // Selected
    let fechaDesde = event.targetElement.value;
    fechaDesde = new Date(fechaDesde);
    this.fechaDesde = this.datePipe.transform(fechaDesde, "yyyy-MM-dd");
    // Como se muestra en el DOM
    let dateForShow = new Date(event.targetElement.value);
    event.targetElement.value = this.datePipe.transform(
      dateForShow,
      "dd/MM/yyyy"
    );
  }

  addEventB(event) {
    // Selected
    let fechaHasta = event.targetElement.value;
    fechaHasta = new Date(fechaHasta);
    this.fechaHasta = this.datePipe.transform(fechaHasta, "yyyy-MM-dd");
    // Como se muestra en el DOM
    let dateForShow = new Date(event.targetElement.value);
    event.targetElement.value = this.datePipe.transform(
      dateForShow,
      "dd/MM/yyyy"
    );
  }

  descargar() {
    console.log(this.fechaDesde, " ", this.fechaHasta);

    // this.apiAdm
    //   .descargarTalleres(this.fechaDesde, this.fechaHasta)
    //   .subscribe(x => {
    //     console.log("Descargado: ", x);
    //   });

    window.open(
      "http://ctrlztest.com.ar/castrol/apirest/downloadexcel.php?fechadesde=" +
        this.fechaDesde +
        "&fechahasta=" +
        this.fechaHasta + "&filtro=" + this.filtro
    );
  }
}



