import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams } from "ionic-angular";
import { ConfirmacionPage } from "../confirmacion/confirmacion";
import { ForzarcitaPage } from "../forzarcita/forzarcita";
import { CalendarioPage } from "../calendario/calendario";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";
import { DatePipe } from "@angular/common";
import { MapPage } from "../map/map";
import { Storage } from "@ionic/storage";

@IonicPage()
@Component({
  selector: "page-gestioncitas",
  templateUrl: "gestioncitas.html"
})
export class GestioncitasPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private apiAdm: ApiAdmProvider,
    private datePipe: DatePipe,
    public storage: Storage
  ) {}

  dataUser: any = {};
  // title: any = "Bienvenido " + this.dataUser.nombre;

  date: any = new Date();
  citas: any = [];
  fullCitas: any = [];
  citasForFilter: any = [];
  selectedDate: any;

  ionViewDidLoad() {
    this.getCitas();
    this.getStorage();
  }

  getCitas() {
    this.apiAdm.getCitas().subscribe(x => {
      if (Array.isArray(x["data"])) {
        console.log("DATA: ", x["data"]);

        this.citas = this.filterBy3Days(x["data"]);
        this.citasForFilter = x["data"];
        this.fullCitas = x["data"];
      }
    });
  }

  // ***********************************************************
  // ---------------- Calendario
  // ***********************************************************

  addEvent(a, event) {
    // Filtra según fecha que se selecciona
    let date = this.dateToNum(event.targetElement.value);
    this.citas = this.filterByDate(date);
    // Como se muestra en el DOM
    let dateForShow = new Date(event.targetElement.value);
    event.targetElement.value = this.datePipe.transform(
      dateForShow,
      "dd/MM/yyyy"
    );
  }

  numbTable(i) {
    // numeracion de las tablas
    var index = this.citas.findIndex(x => x.tallerId === i);
    return index + 1;
  }

  dateToNum(fecha) {
    let date = new Date(fecha);
    let datenum = this.datePipe.transform(date, "yyyyMMdd");
    return +datenum;
  }

  // ***********************************************************
  // ---------------- Filtros
  // ***********************************************************

  filterByDate(fecha) {
    let filtrado = this.fullCitas.filter(item => {
      // transforma en numero la fecha que me llega de la base
      let a = new Date(item.diapreferido.replace(/-/g, "/"));
      let b = this.datePipe.transform(a, "yyyyMMdd");
      let toDate = +b;

      return toDate == fecha;
    });
    return filtrado;
  }

  filterBy3Days(array) {
    // sabado = 6
    // domingo = 0
    // lunes = 1
    // martes = 2
    // miercoles = 3
    // jueves = 4
    // viernes = 5

    var filtrado;
    // Re-trasnformo la fecha para que me quede a las 00:00:00, sino no me funciona bien el filtro
    var date = this.datePipe.transform(this.date, "yyyy-MM-dd");
    var myDate = new Date(date.replace(/-/g, "/"));
    // ------------------------------------- DOMINGO, LUNES, MARTES
    if (
      this.date.getDay() == 0 ||
      this.date.getDay() == 1 ||
      this.date.getDay() == 2
    ) {
      // devolvé data de 3 días siguientes
      let next3 = this.getNextDay(this.date, 3);

      filtrado = array.filter(item => {
        var diapreferidoDate = new Date(item.diapreferido.replace(/-/g, "/"));

        return diapreferidoDate >= next3 && diapreferidoDate <= next3;
      });
    }
    // -------------------------------------
    // ------------------------------------- SÁBADO
    if (this.date.getDay() == 6) {
      // devolvé data de 4 días siguientes
      let next4 = this.getNextDay(this.date, 4);
      filtrado = array.filter(item => {
        var diapreferidoDate = new Date(item.diapreferido.replace(/-/g, "/"));

        return diapreferidoDate >= next4 && diapreferidoDate <= next4;
      });
    }
    // ------------------------------------- MIERCOLES, JUEVES Y VIERNES
    if (
      this.date.getDay() == 3 ||
      this.date.getDay() == 4 ||
      this.date.getDay() == 5
    ) {
      // devolvé data de 5 días siguientes
      var next5 = this.getNextDay(this.date, 5);

      filtrado = array.filter(item => {
        var diapreferidoDate = new Date(item.diapreferido.replace(/-/g, "/"));
        // return diapreferidoDate <= next5 && diapreferidoDate >= myDate;
        return diapreferidoDate >= next5 && diapreferidoDate <= next5;
      });
    }
    // -------------------------------------

    console.log("Filt: ", filtrado);

    // let dia3

    // filtrado.filter(item => {

    // })

    return filtrado;
  }

  getNextDay(date, num) {
    let nextDay = new Date();
    nextDay.setDate(date.getDate() + num);
    let formatDateA = this.datePipe.transform(nextDay, "yyyy-MM-dd");
    let formatDateB = new Date(formatDateA.replace(/-/g, "/"));
    return formatDateB;
  }

  // ***********************************************************
  // ---------------- Set Status
  // ***********************************************************

  setStatus(num) {
    switch (num) {
      case 1:
        return "Pendiente";
      case 2:
        return "Pospuesto";
      case 3:
        return "No acepta preguntas";
      case 4:
        return "Cerrado";
      default:
        break;
    }
  }

  setConfirm(num) {
    switch (num) {
      case "1":
        return "Pendiente";
      case "2":
        return "Confirmó";
      case "3":
        return "Canceló";
      default:
        break;
    }
  }

  setTurno(turno) {
    switch (turno) {
      case "1":
        return "Mañana";
      case "2":
        return "Tarde";
      case "3":
        return "Noche";
      default:
        break;
    }
  }

  // ***********************************************************
  // ---------------- Nav
  // ***********************************************************

  confirmar(cita) {
    this.navCtrl.push(ConfirmacionPage, {
      cita: cita,
      citas: this.fullCitas
    });
  }

  modificar(cita) {
    this.navCtrl.push(ConfirmacionPage, {
      cita: cita,
      citas: this.fullCitas,
      editionMode: true
    });
  }

  forzar() {
    this.navCtrl.push(ForzarcitaPage);
  }

  goToCalendar() {
    this.navCtrl.push(CalendarioPage);
  }

  goToMap() {
    this.navCtrl.push(MapPage, {
      citas: this.fullCitas
    });
  }

  getStorage() {
    this.storage.get("_user_").then(val => {
      this.dataUser = val;
      console.log("Storage: ", val);
    });
  }
}



