import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController
} from "ionic-angular";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";
import { EditarPermisosPage } from "../editar-permisos/editar-permisos";

@IonicPage()
@Component({
  selector: "page-permisos",
  templateUrl: "permisos.html"
})
export class PermisosPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public apiAdm: ApiAdmProvider,
    public alertCtrl: AlertController
  ) {}

  estado: string;
  usuarios: any = [];
  read: boolean = true;
  // ----
  usuarioScout: string;
  passwordScout: string;
  emailScout: string;
  activoScout: string;
  // ----
  usuarioBrig: string;
  passwordBrig: string;
  emailBrig: string;
  activoBrig: string;

  ionViewWillEnter() {
    this.getUsers();
  }

  getUsers() {
    this.apiAdm.getUsers().subscribe(x => {
      this.usuarios = x["data"];
      console.log(x["data"]);
    });
  }

  alta(num) {
    if (num == 2) {
      // SCOUTER
      this.apiAdm
        .altaUsuario(
          this.usuarioScout,
          this.passwordScout,
          this.emailScout,
          this.estado,
          this.activoScout,
          num
        )
        .subscribe(x => {
          this.done();
        });
    } else {
      // BRIGADA
      this.apiAdm
        .altaUsuario(
          this.usuarioBrig,
          this.passwordBrig,
          this.emailBrig,
          this.estado,
          this.activoBrig,
          num
        )
        .subscribe(x => {
          this.done();
        });
    }
    this.getUsers();
  }

  // ALERTS

  done() {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message: "¡Usuario creado con éxito!",

      buttons: [
        {
          text: "Ok",
          handler: data => {
            this.estado = "";
            // ----
            this.usuarioScout = "";
            this.passwordScout = "";
            this.emailScout = "";
            // ----
            this.usuarioBrig = "";
            this.passwordBrig = "";
            this.emailBrig = "";
            // this.navCtrl.setRoot(GestioncitasPage);
          }
        }
      ]
    });
    prompt.present();
  }

  setRol(num) {
    switch (num) {
      case "2":
        return "Scouting";
      case "3":
        return "Brigada";
      default:
        break;
    }
  }

  setActivo(num) {
    switch (num) {
      case "1":
        return "Activo";
      case "0":
        return "Inactivo";
      default:
        break;
    }
  }

  borrarUsuario(user) {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message:
        "¿Está seguro que desea borrar el usuario " + user.nombreusuario + "?",

      buttons: [
        {
          text: "Sí",
          handler: data => {
            this.apiAdm.borrarUsuario(user).subscribe(x => {
              this.alertBorrado();
            });
          }
        },
        {
          text: "Cancelar",
          handler: data => {}
        }
      ]
    });
    prompt.present();
  }

  alertBorrado() {
    const prompt = this.alertCtrl.create({
      // title: 'Login',
      message: "Usuario borrado con éxito.",

      buttons: [
        {
          text: "Ok",
          handler: data => {
            this.ionViewWillEnter();
          }
        }
      ]
    });
    prompt.present();
  }

  editarUsuario(user) {
    this.navCtrl.push(EditarPermisosPage, {
      user: user
    });
  }
}



