import { HttpClient } from "@angular/common/http";
import { Http, Headers, RequestOptions } from "@angular/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { AbstractEmitterVisitor } from "@angular/compiler/src/output/abstract_emitter";

@Injectable()
export class ApiAdmProvider {
  constructor(public http: HttpClient, private httpPost: Http) {}

  // ********** API LINK

  api_url: string = "http://ctrlztest.com.ar/castrol/apirest/";
  api_url_local: string = "../../assets/json/";
  // api_url: string = "/castrol/admin/assets/json/";

  dataUser: any;
  params: any;

  // ***********************************************************
  // ---------------- LOGIN / LOGIN PASS
  // ***********************************************************

  login(usuario: string, password: string): Observable<any> {
    return this.http.get(
      this.api_url + "login.php?usuario=" + usuario + "&clave=" + password
    );
  }

  recuperarClave(data) {
    let api = this.api_url + "recuperarclave.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = JSON.stringify({
      email: data
    });

    console.log(body);

    return this.httpPost.post(api, body, {
      headers: headers,
      withCredentials: true
    });
    //return this.http.get(this.api_url + "recuperarclave.php?email=" + data);
  }

  // ***********************************************************
  // ---------------- GET - Gestion de citas
  // ***********************************************************

  // getCitas(): Observable<any> {
  //   return this.http.get(this.api_url + "arrancaeladmin.php");
  // }

  getCitas(): Observable<any> {
    return this.http.get(
      this.api_url + "traervisitasadmin.php"
      //this.api_url_local + "traercitas.json"
    );
  }

  getBrigadasVisitas(): Observable<any> {
    return this.http.get(
      this.api_url + "traerbrigadasyvisitas.php"
      //  this.api_url_local + "traercitas.json"
    );
  }

  // ***********************************************************
  // ---------------- Post - Gestion de citas
  // ***********************************************************

  confirmarCita(
    visitaid: any,
    horaCita: string,
    diaPreferido: any,
    confirmado: any,
    brigadaid: any
  ): Observable<any> {
    let api = this.api_url + "confirmarvisita.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = JSON.stringify({
      visitaid: visitaid,
      confirmado: confirmado,
      horacita: horaCita,
      diapreferido: diaPreferido,
      brigadaid: brigadaid
    });

    console.log("confirmacion.php: ", body);

    return this.httpPost.post(api, body, {
      headers: headers,
      withCredentials: true
    });
  }

  // ***********************************************************
  // ---------------- Cambiar clave
  // ***********************************************************

  cambiarClave(claveAnterior: string, nuevaClave: string): Observable<any> {
    let api = this.api_url + "cambiarpassword.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = JSON.stringify({
      usuarioid: this.dataUser.usuarioid,
      claveanterior: claveAnterior,
      nuevaclave: nuevaClave
    });

    console.log(body);

    return this.httpPost.post(api, body, {
      headers: headers,
      withCredentials: true
    });
  }

  // ***********************************************************
  // ---------------- Alta usuario
  // ***********************************************************

  altaUsuario(
    usuario: string,
    password: string,
    email: string,
    estado: string,
    activo: string,
    num: number
  ): Observable<any> {
    let api = this.api_url + "altausuario.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = {
      // Ingreso taller 1
      nombreusuario: usuario,
      password: password,
      email: email,
      estado: estado,
      activo: activo,
      rol: num
    };
    console.log("alta", body);
    return this.httpPost.post(api, body, {
      headers: headers,
      withCredentials: true
    });
  }

  // ***********************************************************
  // ---------------- CALENDARIO
  // ***********************************************************

  getFechasOcupadas(): Observable<any> {
    return this.http.get(this.api_url + "traerfechas.php");
  }

  // ***********************************************************
  // ---------------- Guardar taller
  // ***********************************************************

  boolToNumber(value) {
    if (value == true) {
      return 1;
    } else if (value == false) {
      return 0;
    }
  }

  guardarVisita(
    // Agendar cita
    tallerId: any,
    horaCita: any,
    diaPreferido: any,
    confirmarNombreContacto: string,
    confirmarTelefonoTaller: number,
    posponerMotivo: string,
    otroMotivo: string,
    status: any,
    turno: string
  ): Observable<any> {
    let api = this.api_url + "crearvisita.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = [
      {
        // Agendar cita
        tallerid: tallerId,
        horacita: horaCita,
        diapreferido: diaPreferido,
        confirmarnombrecontacto: confirmarNombreContacto,
        confirmartelefonotaller: confirmarTelefonoTaller,
        posponermotivo: posponerMotivo,
        otromotivo: otroMotivo,
        status: status,
        turno: turno
      }
    ];

    for (var i = 0; i < body.length; i++) {
      var obj = body[i];
      for (var key in obj) {
        // var name = key;
        // var value = obj[key];
        if (typeof obj[key] == "boolean") {
          obj[key] = this.boolToNumber(obj[key]);
        }
      }
    }

    let bodyB = JSON.stringify(body[0]);

    console.log("Body 1: ", bodyB);

    return this.httpPost.post(api, bodyB, {
      headers: headers,
      withCredentials: true
    });
  }

  guardarTaller(
    // Ingreso taller 1
    tallerNombre: string,
    telefono: number,
    calle: string,
    numero: number,
    colonia: string,
    delegacion: string,
    estado: string,
    isInGoogle: boolean | number,
    direccion: string,
    // Ingreso taller 2
    motivos: string,
    abierto: number,
    aceptaPreguntas: number,
    // Preguntas 1
    numCambAceite: number,
    numMecanicos: number,
    numeroFosas: number,
    stockTambores: number,
    numEquLubricacion: number,
    fotoInterior: any,
    fotoExterior: any,
    contactoEntrenamiento: string,
    telefonoEntrenamiento: number,
    emailEntrenamiento: string,
    contactoComercial: string,
    telefonoComercial: number,
    emailComercial: string
  ): Observable<any> {
    let api = this.api_url + "creartaller.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = [
      {
        // Ingreso taller 1
        tallernombre: tallerNombre,
        telefono: telefono,
        calle: calle,
        numero: numero,
        colonia: colonia,
        delegacion: delegacion,
        estado: estado,
        isingoogle: isInGoogle,
        direccion: direccion,
        // Ingreso taller 2
        motivos: motivos,
        abierto: abierto,
        aceptapreguntas: aceptaPreguntas,
        // Preguntas 1
        numcambaceite: numCambAceite,
        nummecanicos: numMecanicos,
        numeroFosas: numeroFosas,
        stocktambores: stockTambores,
        numequlubricacion: numEquLubricacion,
        fotointerior: fotoInterior,
        fotoexterior: fotoExterior,
        contactoentrenamiento: contactoEntrenamiento,
        telefonoentrenamiento: telefonoEntrenamiento,
        emailentrenamiento: emailEntrenamiento,
        contactocomercial: contactoComercial,
        telefonocomercial: telefonoComercial,
        emailcomercial: emailComercial
      }
    ];

    for (var i = 0; i < body.length; i++) {
      var obj = body[i];
      for (var key in obj) {
        // var name = key;
        // var value = obj[key];
        if (typeof obj[key] == "boolean") {
          obj[key] = this.boolToNumber(obj[key]);
        }
      }
    }

    let bodyB = JSON.stringify(body[0]);

    console.log("Body 1: ", bodyB);

    return this.httpPost.post(api, bodyB, {
      headers: headers,
      withCredentials: true
    });
  }

  getUsers(): Observable<any> {
    return this.http.get(this.api_url + "traerusuarios.php");
  }
  
  getUsersBackOffice(): Observable<any> {
    return this.http.get(this.api_url + "traerusuariosback.php");
  }

  borrarUsuario(usuario: any): Observable<any> {
    let api = this.api_url + "borrarusuario.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = JSON.stringify({
      usuarioid: usuario.usuarioid
    });

    return this.httpPost.post(api, body, {
      headers: headers,
      withCredentials: true
    });
  }

  editarUsuario(usuarioid,
    nombreusuario: any,
    email: string,
    rol: string,
    clave: string,
    estado: string,
    activo: string
  ): Observable<any> {
    let api = this.api_url + "actualizarusuario.php";

    var headers = new Headers();
    headers.append("Access-Control-Allow-Origin", "*");
    headers.append("Access-Control-Allow-Methods", "POST, GET, OPTIONS, PUT");
    headers.append("Accept", "application/json");
    headers.append("content-type", "application/json");

    const requestOptions = new RequestOptions({ headers: headers });

    var body = JSON.stringify({
      usuarioid: usuarioid,
      nombreusuario: nombreusuario,
      email: email,
      rol: rol,
      clave: clave,
      estado: estado,
      activo: activo
    });
    console.log("body",body);
    return this.httpPost.post(api, body, {
      headers: headers,
      withCredentials: true
    });
  }

  getReagendar(): Observable<any> {
    return this.http.get(this.api_url + "traervisitasareagendar.php");
  }
}



