import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController
} from "ionic-angular";
import { LoginPage } from "../login/login";
import { GestioncitasPage } from "../gestioncitas/gestioncitas";
import { SidemenuPage } from "../sidemenu/sidemenu";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";

@IonicPage()
@Component({
  selector: "page-cambiarclave",
  templateUrl: "cambiarclave.html"
})
export class CambiarclavePage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public apiAdm: ApiAdmProvider
  ) {}

  nuevaClave;
  repetirClave;
  claveAnterior;

  ionViewDidLoad() {
    console.log("ionViewDidLoad CambiarclavePage");
  }

  goToLogin() {
    if (this.nuevaClave === this.repetirClave) {
      this.apiAdm
        .cambiarClave(this.claveAnterior, this.nuevaClave)
        .subscribe(x => {
          this.succes();
        });
    } else {
      this.errorPass();
    }
  }

  goBack() {
    this.navCtrl.setRoot(SidemenuPage);
  }

  // *****************************************
  // ******* POP UPS
  // *****************************************

  errorPass() {
    let alert = this.alertCtrl.create({
      title: "",
      message: "La contraseña no coincide",
      buttons: [
        {
          text: "Ok",
          handler: () => {
            console.log("Ok");
          }
        }
      ]
    });

    alert.present();
  }

  succes() {
    let alert = this.alertCtrl.create({
      title: "",
      message: "Tu clave fue modificada con éxito",
      buttons: [
        {
          text: "Ok",
          handler: () => {
            this.navCtrl.setRoot(SidemenuPage);
          }
        }
      ]
    });

    alert.present();
  }
}



