import { Component, ViewChild } from "@angular/core";
import { Platform, Nav } from "ionic-angular";
import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";

import { LoginPage } from "../pages/login/login";
import { GestioncitasPage } from "../pages/gestioncitas/gestioncitas";
import { CambiarclavePage } from "../pages/cambiarclave/cambiarclave";
import { PermisosPage } from "../pages/permisos/permisos";
import { BasetalleresPage } from "../pages/basetalleres/basetalleres";

@Component({
  templateUrl: "app.html"
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = LoginPage;

  pages: Array<{ title: string; component: any }>;

  constructor(
    platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen
  ) {
    // used for an example of ngFor and navigation
    this.pages = [
      { title: "Gestión de citas", component: GestioncitasPage },
      { title: "Cambiar clave", component: CambiarclavePage },
      { title: "Editar permisos", component: PermisosPage },
      { title: "Descargar Base de Talleres", component: BasetalleresPage },
      { title: "Salir", component: LoginPage }
    ];

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



