import { Component, ViewChild } from "@angular/core";
import { IonicPage, NavController, NavParams, Nav } from "ionic-angular";
import { CambiarclavePage } from "../cambiarclave/cambiarclave";
import { LoginPage } from "../login/login";
import { GestioncitasPage } from "../gestioncitas/gestioncitas";
import { PermisosPage } from "../permisos/permisos";
import { DescargarTalleresPage } from "../descargar-talleres/descargar-talleres";
import { ReagendarcitasPage } from "../reagendarcitas/reagendarcitas";
import { PermisosbkPage } from "../permisosbk/permisosbk";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";

@IonicPage()
@Component({
  selector: "page-sidemenu",
  templateUrl: "sidemenu.html"
})
export class SidemenuPage {
  constructor(public navCtrl: NavController, public apiAdm: ApiAdmProvider,public navParams: NavParams) {}

  rootPage: any;

  @ViewChild(Nav) nav: Nav;
  regularUser: boolean = true;
  dataUser: any = [];
  usuario: string;
  usuarioid: string;

  ionViewWillEnter(){
    if(this.apiAdm.params == "superadmin"){
      this.rootPage = PermisosbkPage;
      this.regularUser = false;
    }
    else{
      this.rootPage = GestioncitasPage;
      this.regularUser = true;
    }
  }

  ionViewDidLoad() {
    this.usuario = this.navParams.get("usuario");
    this.usuarioid = this.navParams.get("usuarioid");
  }

  goToGestion() {
    this.navCtrl.setRoot(SidemenuPage);
  }

  goToCambiarClave() {
    this.navCtrl.push(CambiarclavePage);
  }

  goToEditarPermisos() {
    this.navCtrl.push(PermisosPage);
  }

  descargarTalleres() {
    this.navCtrl.push(DescargarTalleresPage);
  }

  goToReagendar() {
    this.navCtrl.push(ReagendarcitasPage);
  }

  logOut() {
    this.navCtrl.setRoot(LoginPage);
  }
}



