import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController,
  ModalController
} from "ionic-angular";
import { GestioncitasPage } from "../gestioncitas/gestioncitas";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";
import { SidemenuPage } from "../sidemenu/sidemenu";
import { DatePipe } from "@angular/common";
import { CalendarioPage } from "../calendario/calendario";

@IonicPage()
@Component({
  selector: "page-forzarcita",
  templateUrl: "forzarcita.html"
})
export class ForzarcitaPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    private datePipe: DatePipe,
    public modalCtrl: ModalController,
    public apiAdm: ApiAdmProvider
  ) {}
  horaCita: any;
  confirmarNombre: any;
  confirmarTelefono: any;
  citas: any = [];
  repetir: boolean = false;
  // Ingreso taller 1
  tallerNombre: string = ""; // ok
  telefono: number = null; // ok
  calle: string = ""; // ok
  numero: number = null; // ok
  colonia: string = ""; // ok
  delegacion: string = ""; // ok
  estado: string = ""; // ok
  isInGoogle: boolean | number = false;
  direccion: string = "";
  // Ingreso taller 2
  motivos: string = "";
  abierto: number = null;
  aceptaPreguntas: number = null;
  // Preguntas 1
  numCambAceite: number = null; // ok
  numMecanicos: number = null; // ok
  numeroFosas: number = null; // ok
  stockTambores: number = null; // ok
  numEquLubricacion: number = null; // ok
  fotoInterior: any = "";
  fotoExterior: any = "";
  contactoEntrenamiento: string = ""; // ok
  telefonoEntrenamiento: number = null; // ok
  emailEntrenamiento: string = ""; // ok
  contactoComercial: string = ""; // ok
  telefonoComercial: number = null; // ok
  emailComercial: string = ""; // ok
  // Agendar cita
  diaPreferido: string = "";
  confirmarNombreContacto: string = "";
  confirmarTelefonoTaller: number = null;
  posponerMotivo: string = "";
  otroMotivo: string = "";
  // -- solo para mostrar
  diaPreferidoForShow: any = "DD/MM/AAAA"; // <--

  // IMPORTANTE: Falta la dirección, fecha y hora a este form

  ionViewWillEnter() {
    console.log("entra a forzar", this.apiAdm.params);
    if (
      this.apiAdm.params != null &&
      this.apiAdm.params.desde == "calendario"
    ) {
      var fechadata = this.apiAdm.params.data;
      //console.log("fecha data", fechadata);
      this.diaPreferido = fechadata.selectedDateSend;
    }
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad ForzarcitaPage");
  }

  goBack() {
    this.navCtrl.pop();
  }

  repContacto() {
    console.log(this.repetir);

    if (this.repetir) {
      this.contactoComercial = this.contactoEntrenamiento;
      this.telefonoComercial = this.telefonoEntrenamiento;
      this.emailComercial = this.emailEntrenamiento;
    }
  }

  save() {
    // let status = "Pendiente";
    // if (value == 1) {
    //   status = "Pendiente";
    // } else {
    //   status = "Pospuesto";
    // }

    this.apiAdm
      .guardarTaller(
        // Ingreso taller 1
        this.tallerNombre,
        this.telefono,
        this.calle,
        this.numero,
        this.colonia,
        this.delegacion,
        this.estado,
        this.isInGoogle,
        this.direccion,
        // Ingreso taller 2
        this.motivos,
        this.abierto,
        this.aceptaPreguntas,
        // Preguntas 1
        this.numCambAceite,
        this.numMecanicos,
        this.numeroFosas,
        this.stockTambores,
        this.numEquLubricacion,
        this.fotoInterior,
        this.fotoExterior,
        this.contactoEntrenamiento,
        this.telefonoEntrenamiento,
        this.emailEntrenamiento,
        this.contactoComercial,
        this.telefonoComercial,
        this.emailComercial
        // // Agendar cita
        // this.horaCita,
        // this.diaPreferido,
        // this.confirmarNombreContacto,
        // this.confirmarTelefonoTaller,
        // this.posponerMotivo,
        // this.otroMotivo,
        // status
      )
      .subscribe(x => {
        console.log("Guardado: ", JSON.parse(x["_body"])["data"]);
        let status = "1";
        this.apiAdm
          .guardarVisita(
            JSON.parse(x["_body"])["data"],
            this.horaCita,
            this.diaPreferido,
            this.confirmarNombre,
            this.confirmarTelefono,
            status,
            "",
            "",
            ""
          )
          .subscribe(x => {
            this.agendarAlert();
          });

        // if (value == 1) {
        //   this.agendarAlert();
        // } else {
        //   this.citaPospuesta();
        // }
      });
  }

  // ***********************************************************
  // ---------------- ALERTS
  // ***********************************************************

  agendarAlert() {
    const confirm = this.alertCtrl.create({
      message: "Taller agendado correctamente. Muchas gracias.",
      buttons: [
        {
          text: "Ok",
          handler: () => {
            this.navCtrl.setRoot(SidemenuPage);
          }
        }
      ]
    });
    confirm.present();
  }

  goToCalendario() {
    this.navCtrl.push(CalendarioPage, true);
  }

  dateForShow(date) {
    if (date) {
      // Date for show
      let dateForShow = new Date(date.replace(/-/g, "/"));
      let dateForShowB = this.datePipe.transform(dateForShow, "dd/MM/yyyy");
      return dateForShowB;
    } else {
      return "DD/MM/AAAA";
    }
  }
}



