import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Geolocation } from "@ionic-native/geolocation";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

declare var google: any;

@Injectable()
export class GoogleMapsProvider {
  constructor(public http: HttpClient, private geolocation: Geolocation) {}

  // ***********************************************************
  // ---------------- INIT TODO
  // ***********************************************************

  mapElement: any;
  map: any;
  markers: any = [];
  apiKey: string = "AIzaSyAF2VM1_XBDzkcEH5f37EBWpjGtnF15lbQ";
  myPosition: any;
  infoWindow: any = [];

  initMap(mapElement: any) {
    let mapProduct = mapElement;

    return new Promise(resolve => {
      this.geolocation.getCurrentPosition().then(
        position => {
          let myPosition = new google.maps.LatLng(
            // position.coords.latitude,
            // position.coords.longitude
            -34.6008031,
            -58.5011004
          );

          let mapOptions = {
            center: myPosition,
            zoom: 12,
            mapTypeId: google.maps.MapTypeId.ROADMAP,
            streetViewControl: false,
            disableDefaultUI: false,
            fullscreenControl: false,
            zoomControl: false,
            mapTypeControl: false,
            scaleControl: false,
            rotateControl: false,
            styles: [
              { featureType: "poi", stylers: [{ visibility: "off" }] }
              // { featureType: "transit", stylers: [{ visibility: "off" }] },
              // {
              //   featureType: "landscape.natural",
              //   stylers: [{ visibility: "off" }]
              // }
            ]
          };

          this.map = new google.maps.Map(mapProduct, mapOptions);
          resolve(true);
        },
        // Here is the error catching that needs to be added
        err => {
          console.log(" Error : " + JSON.stringify(err));
        }
      );
    });
  }

  parseLocation(citas) {
    let geocoder = new google.maps.Geocoder();
    let coords = [];

    if (citas) {
      for (let cita of citas) {
        var direccion = cita.direccion;

        geocoder.geocode({ address: direccion }, (results, status) => {
          console.log(results[0]);

          if (results[0]) {
            coords = results[0].geometry.location;
            this.addMarker(coords, cita);
          }
        });
      }
    } else {
      console.log("Error, no hay array");
    }
  }

  addMarker(coords, cita) {
    let dirs = {
      lat: coords.lat(),
      lng: coords.lng()
    };

    // --------------
    let markerColor;
    if (cita.isingoogle === "1") {
      markerColor = "marker-verde.png";
    } else {
      markerColor = "marker-rojo.png";
    }
    // --------------
    let status = this.setStatus(cita.status);
    // --------------

    let marker = new google.maps.Marker({
      position: dirs,
      map: this.map,
      // animation: google.maps.Animation.DROP,
      icon: "../../assets/imgs/" + markerColor,
      // Info de los talleres
      tallerid: cita.tallerid,
      visitaid: cita.visitaid,
      tallernombre: cita.tallernombre,
      direccion: cita.direccion,
      telefono: cita.telefono,
      nombrecontacto: cita.contactocomercial,
      status: status,
      diapreferido: cita.diapreferido,
      horacita: cita.horacita
    });

    marker.setMap(this.map);

    this.addInfoWindow(marker);
  }

  cita: any;
  messageSource: any = new BehaviorSubject("Error");
  currentMessage: any = this.messageSource.asObservable();

  addInfoWindow(marker) {
    // for (let mar of marker) {
    let infoWindowContent =
      '<div id="content"><h4>' +
      marker.tallernombre +
      "</h4>" +
      "<p>" +
      marker.direccion +
      "</p>" +
      "<p>" +
      marker.telefono +
      "</p>" +
      "<p>" +
      marker.nombrecontacto +
      "</p>" +
      "<p>" +
      marker.status +
      "</p>" +
      "<p>" +
      marker.diapreferido +
      "</p>" +
      "</div>";

    let infoWindow = new google.maps.InfoWindow({
      content: infoWindowContent
    });

    marker.addListener("click", () => {
      this.closeAllInfoWindows();
      infoWindow.open(this.map, marker);

      // ----
      this.messageSource = new BehaviorSubject(infoWindow.anchor);
      this.currentMessage = this.messageSource.asObservable();
    });
    // ----

    this.infoWindow.push(infoWindow);
  }
  //

  changeMessage(message: any) {
    this.messageSource.next(message);
  }

  closeAllInfoWindows() {
    for (let window of this.infoWindow) {
      window.close();
    }
  }

  setStatus(num) {
    switch (num) {
      case "1":
        return "Pendiente";
      case "2":
        return "Pospuesto";
      case "3":
        return "No acepta preguntas";
      case "4":
        return "Cerrado";
      default:
        break;
    }
  }
}



