import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams } from "ionic-angular";
import { ApiAdmProvider } from "../../providers/api-adm/api-adm";
import { ConfirmacionPage } from "../confirmacion/confirmacion";
import { MapPage } from "../map/map";

@IonicPage()
@Component({
  selector: "page-reagendarcitas",
  templateUrl: "reagendarcitas.html"
})
export class ReagendarcitasPage {
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public apiAdm: ApiAdmProvider
  ) {}

  citasReagendar: any = [];

  ionViewDidLoad() {
    this.getReagendar();
  }

  getReagendar() {
    this.apiAdm.getReagendar().subscribe(x => {
      this.citasReagendar = x["data"];
      console.log(x["data"]);
    });
  }

  reagendar(cita) {
    this.navCtrl.push(ConfirmacionPage, {
      cita: cita,
      reagendar: true
    });
  }

  modificar(cita) {
    this.navCtrl.push(ConfirmacionPage, {
      cita: this.citasReagendar,
      // citas: this.fullCitas,
      editionMode: true
    });
  }

  goToMap() {
    this.navCtrl.push(MapPage, {
      citas: this.citasReagendar
    });
  }

  // ---

  // ***********************************************************
  // ---------------- Set Status
  // ***********************************************************

  setStatus(num) {
    switch (num) {
      case 1:
        return "Pendiente";
      case 2:
        return "Pospuesto";
      case 3:
        return "No acepta preguntas";
      case 4:
        return "Cerrado";
      default:
        break;
    }
  }

  setConfirm(num) {
    switch (num) {
      case "1":
        return "Pendiente";
      case "2":
        return "Confirmó";
      case "3":
        return "Canceló";
      default:
        break;
    }
  }
}



