    PermisosPage,
    BasetalleresPage,
    ConfirmacionPage,
    ForzarcitaPage,
    BasetalleresPage,
    CalendarioPage,
    CambiarclavePage,
    DescargarTalleresPage,
    MapPage,
    SidemenuPage,
    EditarPermisosPage,
    ReagendarcitasPage,
    // ---- Components
    HeaderComponent,
    FooterComponent,
    DatetimeComponent,
    PermisosbkPage,
    EditarPermisosbkPage
